@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

h1, h2, h3 {
  font-weight: 300;
  margin: 30px 0
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}


img {
  width: 100%;
}
.button-nav {
    border-radius: 20px;
    width: 200px;
    background-color: white;
}

.logo {
    width: 100px;
}

.BackHome {
    background-size: 100%;
    background-repeat: no-repeat;
    object-fit: cover;
}

.centrado h1{
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   color: white;
   font-size: 4em;
   font-weight: 100;
   text-align: center;
   width: 100%;
   text-shadow: 
    2px 2px 0 #000,
    -2px -2px 0 #000,  
    2px -2px 0 #000,
    -2px 2px 0 #000,
    2px 2px 0 #000;
  -webkit-text-stroke: 1px rgba(0,0,0,.7);
}

.chefcard {
  margin-bottom: 40px;
}
.chefcard img {
  height: 180px;
  object-fit: cover;
}
#chef-form {
  padding: 20px;
}
#button-form {
  margin: 20px;
}
h3 {
  color: black;
}
.chef-profile {
  width: 980px;
  height: auto;
  position: relative;
  overflow: hidden;
  background: #fff;
  margin: 15px auto 15px 10px;
  box-shadow: 1px 0 3px 2px rgba(0, 0, 0, 0.2);
  float: left;
}
#cover-chef {
  width: 146%;
  height: 73%;
  position: absolute;
  transform: rotate(21deg);
  top: -59%;
  left: -4%;
  background: rgba(40, 180, 99);
  background: -webkit-gradient(
    left bottom,
    right top,
    color-stop(0%, rgba(40, 180, 99)),
    color-stop(100%, rgba(34, 153, 84))
  );
  background: linear-gradient(
    45deg,
    rgba(40, 180, 99) 0%,
    rgba(34, 153, 84) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00a5d7', endColorstr='#007da3', GradientType=1 );
  opacity: 90%;
}
#shadow {
  clear: both;
  height: 10px;
  bottom: 200px;
  overflow: hidden;
  margin: 356px 0 0 0;
}
#shadow img {
  width: 80%;
  height: 10px;
  padding: 0 0px 0 20px;
  opacity: 0.5;
}
#info {
  margin: 280px auto;
  width: 90%;
  border-top: 1px solid #ccc;
  padding: 10px 0 0 0;
}
#info li {
  padding: 2px 0 2px 10px;
  margin: 3px auto;
  font-size: 10pt;
}
#username {
  text-align: center;
  font-size: 1.5em !important;
  line-height: 1;
  font-weight: bold;
}
#avatar {
  width: 200px;
  height: 200px;
  position: absolute;
  z-index: 1;
  border: solid 5px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  overflow: hidden;
  margin: 20px;
  cursor: pointer;
  transition: 0.3s;
}
#info i {
  font-size: 14pt;
  margin: 0 5px;
  color: #057091;
}
#avatar img {
  width: 100%;
}
#avatar:hover {
  border: solid 3px rgba(255, 255, 255, 0.6);
}
#boton {
  position: absolute;
  top: 20px;
  right: 23px;
}
#add-comment {
    padding: 40px;
}
