/* Cards */
.cards {
  display: flex;
  align-items: center;
  padding: 40px;
  overflow-x: scroll;
  min-height: 100vh;
}
.card {
  min-width: 300px;
  margin-left: -80px;
  border-radius: 40px;
  padding: 30px;
  box-shadow: -4px 4px 26px 2px hsla(184, 50%, 20%, .1);
  background: linear-gradient(to right bottom, hsl(0, 0%, 98%), hsl(0, 0%, 96%));
  transition: all .3s;
  position: relative;
}
.card:first-child {
  margin-left: 0;
}
.card:hover,
.card:focus-within {
  transform: translateY(-20px)
}
.card:hover+.card,
.card:focus-within+.card {
  transform: translateX(80px);
}

